.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App {
  font-size: 20px;
  text-align: center;
  padding: 25px;
}

@media only screen and (max-width: 600px) {
  .dice {
      width: 40px;
      height: 40px;
      font-size: 20px;
      line-height: 40px;
  }

  .dice-dot {
      width: 8px;
      height: 8px;
  }
}

.footer {
  margin-top: 50px;
  font-size: 16px;
  color: #555;
}